<script>
  import { get } from "../utils";
  import { auth } from "../Auth";
  import Section from "../components/layout/Section.svelte";

  let promise = get("/games", $auth.token);
</script>

<Section>
  {#await promise}
    <p>...waiting</p>
  {:then games}
    <ul>
      {#each games as { name }}
        <li>{name}</li>
      {/each}
    </ul>
  {:catch error}
    <p style="color: red">{error.message}</p>
  {/await}
</Section>
